/*
 *
 * RecoveryRequest reducer
 *
 */

import {
  FETCH_RECOVERIES,
  RESET_RECOVERY_FORM,
  GET_ALL_DATA,
  GET_DATA,
  GET_RECOVERY,
  ADD_RECOVERY,
  DELETE_RECOVERY,
  SET_RECOVERIES_LOADING,
  SET_RECOVERIES_SUBMITTING,
  SET_RECOVERIES_FORM_ERRORS,
  RECOVERY_FORM_CHANGE,
  RECOVERY_EDIT_CHANGE,
  DEACTIVATE_RECOVERY
} from './constants'

const initialState = {
  recoveries: [],
  allData: [],
  data: [],
  total: 1,
  params: {},
  selectedRecovery: null,
  recoveryFormData: {
  },
  formErrors: {},
  isSubmitting: false,
  isLoading: false
}

const recoveriesReducer = (state = initialState, action) => {
  switch (action.type) {
    case FETCH_RECOVERIES:
      return {
        ...state,
        recoveries: action.payload
      }
    case GET_ALL_DATA:
      return { ...state, allData: action.data }
    case GET_DATA:
      return {
        ...state,
        data: action.data,
        total: action.totalPages,
        params: action.params
      }
    case GET_RECOVERY:
      return { ...state, selectedRecovery: action.selectedRecovery }
    case ADD_RECOVERY:
      return { ...state }
    case DELETE_RECOVERY:
      return { ...state }
    case DEACTIVATE_RECOVERY:
      return { ...state }
    case RECOVERY_FORM_CHANGE:
      return {
        ...state,
        recoveryFormData: { ...state.recoveryFormData, ...action.payload },
        formErrors: {}
      }
    case RECOVERY_EDIT_CHANGE:
      return {
        ...state,
        selectedRecovery: { ...state.selectedRecovery, ...action.payload },
        formErrors: {}
      }
    case RESET_RECOVERY_FORM:
      return {
        ...state,
        recoveryFormData: { ...initialState.recoveryFormData },
        formErrors: {}
      }
    case SET_RECOVERIES_SUBMITTING:
      return {
        ...state,
        isSubmitting: action.payload
      }
    case SET_RECOVERIES_LOADING:
      return {
        ...state,
        isLoading: action.payload
      }
    case SET_RECOVERIES_FORM_ERRORS:
      return {
        ...state,
        formErrors: action.payload
      }
    default:
      return state
  }
}

export default recoveriesReducer
