/*
 *
 * Vendors reducer
 *
 */

import {
  FETCH_VENDORS,
  RESET_VENDOR_FORM,
  GET_ALL_DATA,
  GET_DATA,
  GET_VENDOR,
  ADD_VENDOR,
  DELETE_VENDOR,
  SET_VENDORS_LOADING,
  SET_VENDORS_SUBMITTING,
  SET_VENDORS_FORM_ERRORS,
  VENDOR_FORM_CHANGE,
  VENDOR_EDIT_CHANGE,
  FETCH_EMPLOYEES,
  // Vendor Employee
  RESET_VENDOR_EMPLOYEE_FORM,
  GET_EMPLOYEE_ALL_DATA,
  GET_EMPLOYEE_DATA,
  GET_VENDOR_EMPLOYEE,
  ADD_VENDOR_EMPLOYEE,
  DELETE_VENDOR_EMPLOYEE,
  SET_VENDOR_EMPLOYEES_FORM_ERRORS,
  VENDOR_EMPLOYEE_FORM_CHANGE,
  VENDOR_EMPLOYEE_EDIT_CHANGE
} from './constants'

const initialState = {
  allData: [],
  data: [],
  employees: [],
  total: 1,
  params: {},
  selectedVendor: null,
  vendorFormData: {
  },
  formErrors: {},
  isSubmitting: false,
  isLoading: false,
  employeeAllData: [],
  employeeData: [],
  employeeTotal: 1,
  employeeParams: {},
  selectedVendorEmployee: null,
  vendorEmployeeFormData: {
  },
  employeeFormErrors: {}
}

const vendorsReducer = (state = initialState, action) => {
  switch (action.type) {
    case GET_ALL_DATA:
      return { ...state, allData: action.data }
    case GET_DATA:
      return {
        ...state,
        data: action.data,
        total: action.totalPages,
        params: action.params
      }
    case FETCH_EMPLOYEES:
      return {
        ...state,
        employees: action.data
      }
    case GET_VENDOR:
      return { ...state, selectedVendor: action.selectedVendor }
    case ADD_VENDOR:
      return { ...state }
    case DELETE_VENDOR:
      return { ...state }
    case VENDOR_FORM_CHANGE:
      return {
        ...state,
        vendorFormData: { ...state.vendorFormData, ...action.payload },
        formErrors: {}
      }
    case VENDOR_EDIT_CHANGE:
      return {
        ...state,
        selectedVendor: { ...state.selectedVendor, ...action.payload },
        formErrors: {}
      }
    case RESET_VENDOR_FORM:
      return {
        ...state,
        vendorFormData: { ...initialState.vendorFormData },
        formErrors: {}
      }
    case SET_VENDORS_SUBMITTING:
      return {
        ...state,
        isSubmitting: action.payload
      }
    case SET_VENDORS_LOADING:
      return {
        ...state,
        isLoading: action.payload
      }
    case SET_VENDORS_FORM_ERRORS:
      return {
        ...state,
        formErrors: action.payload
      }
    //  Vendor Employee
    case GET_EMPLOYEE_ALL_DATA:
      return { ...state, employeeAllData: action.data }
    case GET_EMPLOYEE_DATA:
      return {
        ...state,
        employeeData: action.data,
        employeeTotal: action.totalPages,
        employeeParams: action.params
      }
    case GET_VENDOR_EMPLOYEE:
      return { ...state, selectedVendorEmployee: action.selectedEmployee }
    case ADD_VENDOR_EMPLOYEE:
      return { ...state }
    case DELETE_VENDOR_EMPLOYEE:
      return { ...state }
    case VENDOR_EMPLOYEE_FORM_CHANGE:
      return {
        ...state,
        vendorEmployeeFormData: { ...state.vendorEmployeeFormData, ...action.payload },
        formErrors: {}
      }
    case VENDOR_EMPLOYEE_EDIT_CHANGE:
      return {
        ...state,
        selectedVendorEmployee: { ...state.selectedVendorEmployee, ...action.payload },
        employeeFormErrors: {}
      }
    case RESET_VENDOR_EMPLOYEE_FORM:
      return {
        ...state,
        vendorEmployeeFormData: { ...initialState.vendorEmployeeFormData },
        employeeFormErrors: {}
      }
    case SET_VENDOR_EMPLOYEES_FORM_ERRORS:
      return {
        ...state,
        employeeFormErrors: action.payload
      }
    default:
      return state
  }
}

export default vendorsReducer
