/*
 *
 * Employees reducer
 *
 */

import {
  FETCH_EMPLOYEES,
  RESET_EMPLOYEE_FORM,
  GET_ALL_DATA,
  GET_DATA,
  GET_EMPLOYEE,
  ADD_EMPLOYEE,
  DELETE_EMPLOYEE,
  SET_EMPLOYEES_LOADING,
  SET_EMPLOYEES_SUBMITTING,
  SET_EMPLOYEES_FORM_ERRORS,
  EMPLOYEE_FORM_CHANGE,
  EMPLOYEE_EDIT_CHANGE,
  DEACTIVATE_EMPLOYEE, FETCH_AGENTS
} from './constants'

const initialState = {
  employees: [],
  agents: [],
  allData: [],
  data: [],
  total: 1,
  params: {},
  selectedEmployee: null,
  employeeFormData: {
  },
  formErrors: {},
  isSubmitting: false,
  isLoading: false
}

const employeesReducer = (state = initialState, action) => {
  switch (action.type) {
    case FETCH_EMPLOYEES:
      return {
        ...state,
        employees: action.payload
      }
    case GET_ALL_DATA:
      return { ...state, allData: action.data }
    case FETCH_AGENTS:
      return { ...state, agents: action.data }
    case GET_DATA:
      return {
        ...state,
        data: action.data,
        total: action.totalPages,
        params: action.params
      }
    case GET_EMPLOYEE:
      return { ...state, selectedEmployee: action.selectedEmployee }
    case ADD_EMPLOYEE:
      return { ...state }
    case DELETE_EMPLOYEE:
      return { ...state }
    case DEACTIVATE_EMPLOYEE:
      return { ...state }
    case EMPLOYEE_FORM_CHANGE:
      return {
        ...state,
        employeeFormData: { ...state.employeeFormData, ...action.payload },
        formErrors: {}
      }
    case EMPLOYEE_EDIT_CHANGE:
      return {
        ...state,
        selectedEmployee: { ...state.selectedEmployee, ...action.payload },
        formErrors: {}
      }
    case RESET_EMPLOYEE_FORM:
      return {
        ...state,
        employeeFormData: { ...initialState.employeeFormData },
        formErrors: {}
      }
    case SET_EMPLOYEES_SUBMITTING:
      return {
        ...state,
        isSubmitting: action.payload
      }
    case SET_EMPLOYEES_LOADING:
      return {
        ...state,
        isLoading: action.payload
      }
    case SET_EMPLOYEES_FORM_ERRORS:
      return {
        ...state,
        formErrors: action.payload
      }
    default:
      return state
  }
}

export default employeesReducer
