/*
 *
 * AutoServiceRequest reducer
 *
 */

import {
  FETCH,
  RESET_FORM,
  GET_ALL_DATA,
  GET_DATA,
  GET,
  ADD,
  DELETE,
  SET_LOADING,
  SET_SUBMITTING,
  SET_FORM_ERRORS,
  FORM_CHANGE,
  EDIT_CHANGE,
  DEACTIVATE
} from './constants'

const initialState = {
  autoServices: [],
  allData: [],
  data: [],
  total: 1,
  params: {},
  selectedData: null,
  formData: {
  },
  formErrors: {},
  isSubmitting: false,
  isLoading: false
}

const autoServicesReducer = (state = initialState, action) => {
  switch (action.type) {
    case FETCH:
      return {
        ...state,
        autoServices: action.payload
      }
    case GET_ALL_DATA:
      return { ...state, allData: action.data }
    case GET_DATA:
      return {
        ...state,
        data: action.data,
        total: action.totalPages,
        params: action.params
      }
    case GET:
      return { ...state, selectedData: action.selectedData }
    case ADD:
      return { ...state }
    case DELETE:
      return { ...state }
    case DEACTIVATE:
      return { ...state }
    case FORM_CHANGE:
      return {
        ...state,
        formData: { ...state.formData, ...action.payload },
        formErrors: {}
      }
    case EDIT_CHANGE:
      return {
        ...state,
        selectedData: { ...state.selectedData, ...action.payload },
        formErrors: {}
      }
    case RESET_FORM:
      return {
        ...state,
        formData: { ...initialState.formData },
        formErrors: {}
      }
    case SET_SUBMITTING:
      return {
        ...state,
        isSubmitting: action.payload
      }
    case SET_LOADING:
      return {
        ...state,
        isLoading: action.payload
      }
    case SET_FORM_ERRORS:
      return {
        ...state,
        formErrors: action.payload
      }
    default:
      return state
  }
}

export default autoServicesReducer
