import { connectRouter } from 'connected-react-router'
import { reducer as notifications } from 'react-notification-system-redux'
// ** Redux Imports
import { combineReducers } from 'redux'
// ** Reducers Imports
import applicationReducer from '../Application/reducer'
import authenticationReducer from '../Authentication/reducer'
import loginReducer from '../Login/reducer'
import signupReducer from '../SignUp/reducer'
import forgotPasswordReducer from '../ForgotPassword/reducer'
import resetPasswordReducer from '../ResetPassword/reducer'
import navbarReducer from '../navbar/reducer'
import layoutReducer from '../layout/reducer'
import accountReducer from '../Account/reducer'
import usersReducer from '../Users/reducer'
import vendorsReducer from '../Vendors/reducer'
import employeesReducer from '../Employees/reducer'
import recoveriesReducer from '../RecoveryRequest/reducer'
import userCarsReducer from '../UserCars/reducer'
import recoveryCategoriesReducer from '../RecoveryCategory/reducer'
import autoServicesReducer from '../AutoServiceRequest/reducer'
import autoServiceCategoriesReducer from '../AutoServiceCategory/reducer'
import rolesReducer from '../Roles/reducer'

const rootReducer = combineReducers({
  notifications,
  application: applicationReducer,
  authentication: authenticationReducer,
  login: loginReducer,
  signup: signupReducer,
  forgotPassword: forgotPasswordReducer,
  resetPassword: resetPasswordReducer,
  navbar: navbarReducer,
  layout: layoutReducer,
  account: accountReducer,
  users: usersReducer,
  vendors: vendorsReducer,
  employees: employeesReducer,
  recoveries: recoveriesReducer,
  userCars: userCarsReducer,
  recoveryCategories: recoveryCategoriesReducer,
  roles: rolesReducer,
  autoServices: autoServicesReducer,
  autoServiceCategories: autoServiceCategoriesReducer
})

export default rootReducer
