/*
 *
 * RecoveryCategory reducer
 *
 */

import {
  FETCH,
  RESET_FORM,
  GET_ALL_DATA,
  GET_DATA,
  GET,
  ADD,
  DELETE,
  SET_LOADING,
  SET_SUBMITTING,
  SET_FORM_ERRORS,
  FORM_CHANGE,
  EDIT_CHANGE,
  DEACTIVATE
} from './constants'

const initialState = {
  recoveryCategories: [],
  allData: [],
  data: [],
  total: 1,
  params: {},
  selectedRecoveryCategory: null,
  recoveryCategoryFormData: {
  },
  formErrors: {},
  isSubmitting: false,
  isLoading: false
}

const recoveryCategoriesReducer = (state = initialState, action) => {
  switch (action.type) {
    case FETCH:
      return {
        ...state,
        recoveryCategories: action.payload
      }
    case GET_ALL_DATA:
      return { ...state, allData: action.data }
    case GET_DATA:
      return {
        ...state,
        data: action.data,
        total: action.totalPages,
        params: action.params
      }
    case GET:
      return { ...state, selectedRecoveryCategory: action.selectedRecoveryCategory }
    case ADD:
      return { ...state }
    case DELETE:
      return { ...state }
    case DEACTIVATE:
      return { ...state }
    case FORM_CHANGE:
      return {
        ...state,
        recoveryCategoryFormData: { ...state.recoveryCategoryFormData, ...action.payload },
        formErrors: {}
      }
    case EDIT_CHANGE:
      return {
        ...state,
        selectedRecoveryCategory: { ...state.selectedRecoveryCategory, ...action.payload },
        formErrors: {}
      }
    case RESET_FORM:
      return {
        ...state,
        recoveryCategoryFormData: { ...initialState.recoveryCategoryFormData },
        formErrors: {}
      }
    case SET_SUBMITTING:
      return {
        ...state,
        isSubmitting: action.payload
      }
    case SET_LOADING:
      return {
        ...state,
        isLoading: action.payload
      }
    case SET_FORM_ERRORS:
      return {
        ...state,
        formErrors: action.payload
      }
    default:
      return state
  }
}

export default recoveryCategoriesReducer
