/*
 *
 * ResetPassword reducer
 *
 */

import {
  RESET_PASSWORD_CHANGE,
  RESET_PASSWORD_RESET,
  SET_RESET_PASSWORD_FORM_ERRORS,
  SET_RESET_PASSWORD_LOADING
} from './constants'

const initialState = {
  resetFormData: {
    password: '',
    confirmPassword: ''
  },
  formErrors: {},
  isLoading: false
}

const resetPasswordReducer = (state = initialState, action) => {
  switch (action.type) {
    case RESET_PASSWORD_CHANGE:
      return {
        ...state,
        resetFormData: { ...state.resetFormData, ...action.payload }
      }
    case SET_RESET_PASSWORD_FORM_ERRORS:
      return {
        ...state,
        formErrors: action.payload
      }
    case SET_RESET_PASSWORD_LOADING:
      return {
        ...state,
        isLoading: action.payload
      }
    case RESET_PASSWORD_RESET:
      return {
        ...state,
        resetFormData: {
          password: '',
          confirmPassword: ''
        },
        formErrors: {},
        isLoading: false
      }
    default:
      return state
  }
}

export default resetPasswordReducer
