/*
 *
 * Users reducer
 *
 */

import {
  FETCH_USERS,
  RESET_USER_FORM,
  GET_ALL_DATA,
  GET_DATA,
  GET_USER,
  ADD_USER,
  DELETE_USER,
  SET_USERS_LOADING,
  SET_USERS_SUBMITTING,
  SET_USERS_FORM_ERRORS,
  USER_FORM_CHANGE,
  USER_EDIT_CHANGE,
  DEACTIVATE_USER
} from './constants'

const initialState = {
  users: [],
  allData: [],
  data: [],
  total: 1,
  params: {},
  selectedUser: null,
  userFormData: {
  },
  formErrors: {},
  isSubmitting: false,
  isLoading: false
}

const usersReducer = (state = initialState, action) => {
  switch (action.type) {
    case FETCH_USERS:
      return {
        ...state,
        users: action.payload
      }
    case GET_ALL_DATA:
      return { ...state, allData: action.data }
    case GET_DATA:
      return {
        ...state,
        data: action.data,
        total: action.totalPages,
        params: action.params
      }
    case GET_USER:
      return { ...state, selectedUser: action.selectedUser }
    case ADD_USER:
      return { ...state }
    case DELETE_USER:
      return { ...state }
    case DEACTIVATE_USER:
      return { ...state }
    case USER_FORM_CHANGE:
      return {
        ...state,
        userFormData: { ...state.userFormData, ...action.payload },
        formErrors: {}
      }
    case USER_EDIT_CHANGE:
      return {
        ...state,
        selectedUser: { ...state.selectedUser, ...action.payload },
        formErrors: {}
      }
    case RESET_USER_FORM:
      return {
        ...state,
        userFormData: { ...initialState.userFormData },
        formErrors: {}
      }
    case SET_USERS_SUBMITTING:
      return {
        ...state,
        isSubmitting: action.payload
      }
    case SET_USERS_LOADING:
      return {
        ...state,
        isLoading: action.payload
      }
    case SET_USERS_FORM_ERRORS:
      return {
        ...state,
        formErrors: action.payload
      }
    default:
      return state
  }
}

export default usersReducer
