/**
 *
 * Notification
 *
 */

import React from 'react'
import Avatar from '@components/avatar'
import { useSelector } from 'react-redux'
import { toast } from 'react-toastify'

// ** Configs
import themeConfig from '@configs/themeConfig'

const Message = (props) => {
  const { title, message } = props.notification

  return <>
    <div className='toastify-header'>
      <div className='title-wrapper'>
        <Avatar size='sm' color='white' img={themeConfig.app.appLogoImage} />
        <h6 className='toast-title font-weight-bold'>{title}</h6>
      </div>
    </div>
    <div className='toastify-body'>
      <span>{message}</span>
    </div>
  </>
}

const Notification = (props) => {
  const notifications = useSelector(state => state.notifications)
  if (notifications && notifications.length > 0) {
    const notification = notifications[notifications.length - 1]
    const { level } = notification
    if (level === 'success') {
      toast.success(<Message notification={notification} />)
    } else if (level === 'error') {
      toast.error(<Message notification={notification} />)
    } else if (level === 'warning') {
      toast.warn(<Message notification={notification} />)
    } else if (level === 'info') {
      toast.info(<Message notification={notification} />)
    } else {
      toast(message)
    }
  }
  return null
}

export default Notification
