/*
 *
 * Role reducer
 *
 */

import {
  FETCH,
  RESET_FORM,
  GET_ALL_DATA,
  GET_DATA,
  GET,
  ADD,
  DELETE,
  SET_LOADING,
  SET_SUBMITTING,
  SET_FORM_ERRORS,
  FORM_CHANGE,
  EDIT_CHANGE,
  DEACTIVATE
} from './constants'

const initialState = {
  roles: [],
  allData: [],
  rolesConfig: {},
  data: [],
  total: 1,
  params: {},
  selectedRole: {
    membership: {},
    roles: {},
    entityType: {},
    status: {}
  },
  roleFormData: {
  },
  formErrors: {},
  isSubmitting: false,
  isLoading: false
}

const rolesReducer = (state = initialState, action) => {
  switch (action.type) {
    case FETCH:
      return {
        ...state,
        rolesConfig: action.data
      }
    case GET_ALL_DATA:
      return { ...state, allData: action.data }
    case GET_DATA:
      return {
        ...state,
        data: action.data,
        total: action.totalPages,
        params: action.params
      }
    case GET:
      return { ...state, selectedRole: action.selectedRole }
    case ADD:
      return { ...state }
    case DELETE:
      return { ...state }
    case DEACTIVATE:
      return { ...state }
    case FORM_CHANGE:
      return {
        ...state,
        roleFormData: { ...state.roleFormData, ...action.payload },
        formErrors: {}
      }
    case EDIT_CHANGE:
      return {
        ...state,
        selectedRole: { ...state.selectedRole, ...action.payload },
        formErrors: {}
      }
    case RESET_FORM:
      return {
        ...state,
        roleFormData: { ...initialState.roleFormData },
        formErrors: {}
      }
    case SET_SUBMITTING:
      return {
        ...state,
        isSubmitting: action.payload
      }
    case SET_LOADING:
      return {
        ...state,
        isLoading: action.payload
      }
    case SET_FORM_ERRORS:
      return {
        ...state,
        formErrors: action.payload
      }
    default:
      return state
  }
}

export default rolesReducer
