/*
 *
 * UserCars reducer
 *
 */

import {
  FETCH_USERCARS,
  RESET_USERCAR_FORM,
  GET_ALL_DATA,
  GET_DATA,
  GET_USERCAR,
  ADD_USERCAR,
  DELETE_USERCAR,
  SET_USERCARS_LOADING,
  SET_USERCARS_SUBMITTING,
  SET_USERCARS_FORM_ERRORS,
  USERCAR_FORM_CHANGE,
  USERCAR_EDIT_CHANGE,
  DEACTIVATE_USERCAR
} from './constants'

const initialState = {
  userCars: [],
  allData: [],
  data: [],
  total: 1,
  params: {},
  selectedUserCar: null,
  userCarFormData: {
  },
  formErrors: {},
  isSubmitting: false,
  isLoading: false
}

const userCarsReducer = (state = initialState, action) => {
  switch (action.type) {
    case FETCH_USERCARS:
      return {
        ...state,
        userCars: action.payload
      }
    case GET_ALL_DATA:
      return { ...state, allData: action.data }
    case GET_DATA:
      return {
        ...state,
        data: action.data,
        total: action.totalPages,
        params: action.params
      }
    case GET_USERCAR:
      return { ...state, selectedUserCar: action.selectedUserCar }
    case ADD_USERCAR:
      return { ...state }
    case DELETE_USERCAR:
      return { ...state }
    case DEACTIVATE_USERCAR:
      return { ...state }
    case USERCAR_FORM_CHANGE:
      return {
        ...state,
        userCarFormData: { ...state.userCarFormData, ...action.payload },
        formErrors: {}
      }
    case USERCAR_EDIT_CHANGE:
      return {
        ...state,
        selectedUserCar: { ...state.selectedUserCar, ...action.payload },
        formErrors: {}
      }
    case RESET_USERCAR_FORM:
      return {
        ...state,
        userCarFormData: { ...initialState.userCarFormData },
        formErrors: {}
      }
    case SET_USERCARS_SUBMITTING:
      return {
        ...state,
        isSubmitting: action.payload
      }
    case SET_USERCARS_LOADING:
      return {
        ...state,
        isLoading: action.payload
      }
    case SET_USERCARS_FORM_ERRORS:
      return {
        ...state,
        formErrors: action.payload
      }
    default:
      return state
  }
}

export default userCarsReducer
