/*
 *
 * Authentication reducer
 *
 */

import {
  GET_PROFILE,
  SET_AUTH,
  CLEAR_AUTH
} from './constants'

const initialState = {
  authenticated: false,
  isLoading: false,
  userData: {}
}

const authenticationReducer = (state = initialState, action) => {
  switch (action.type) {
    case SET_AUTH:
      return {
        ...state,
        authenticated: true,
        userData: action.data,
        [action.config.storageTokenKeyName]: action[action.config.storageTokenKeyName],
        [action.config.storageRefreshTokenKeyName]: action[action.config.storageRefreshTokenKeyName]
      }
    case CLEAR_AUTH:
      const obj = { ...action }
      delete obj.type
      return {
        ...state,
        authenticated: false,
        userData: {},
        ...obj
      }
    case GET_PROFILE:
      return {
        ...state,
        userData: action.data
      }
    default:
      return state
  }
}

export default authenticationReducer
